@use "./common/_functions" as *;

// --------------------------------------
// @see src/common/_breakpoint.scss
// --------------------------------------
$breakpoints: (
  "pc": "print, screen and (min-width: 768px)",
  "sp": "screen and (max-width: 767px)",
) !default;


// --------------------------------------
// @see src/utilites/_font-size.scss
// --------------------------------------
// px
$font-sizes-px: generate-range(10, 30);
// em
$font-sizes-em: generate-range(1, 10);


// --------------------------------------
// @see src/utilites/_height.scss
// --------------------------------------
// px
$height-px: generate-range(1, 500);

// --------------------------------------
// @see src/utilites/_width.scss
// --------------------------------------
// px
$width-px: generate-range(1, 200);

// %
$width-percent: generate-range(1, 100);

// em
$width-em: generate-range(1, 10);

// --------------------------------------
// @see src/utilites/_basis.scss
// --------------------------------------
// px
$basis-px: generate-range(1, 200);

// %
$basis-percent: generate-range(1, 100);

// em
$basis-em: generate-range(1, 10);

// --------------------------------------
// @see src/utilites/_max-width.scss
// --------------------------------------
// px
$max-width-px: generate-range(1, 200);

// %
$max-width-percent: generate-range(1, 100);

// --------------------------------------
// @see src/utilites/_min-width.scss
// --------------------------------------
// px
$min-width-px: generate-range(1, 200);

// %
$min-width-percent: generate-range(1, 100);


// --------------------------------------
// @see src/utilites/_margin.scss
// --------------------------------------
// px
$margin-px: generate-range(-100, 100, true);

// %
$margin-percent: generate-range(0, 100, true);


// --------------------------------------
// @see src/utilites/_padding.scss
// --------------------------------------
// px
$padding-px: generate-range(0, 100, false);

// %
$padding-percent: generate-range(0, 100, false);



// --------------------------------------
// @see src/utilites/_gap.scss
// --------------------------------------
$gap-px: generate-range(0, 100);
$gap-percent: generate-range(0, 100);


// --------------------------------------
// @see src/utilites/_order.scss
// --------------------------------------
$order-values: generate-range(0, 10);
